import React, { Fragment, useState } from 'react';
import PlayerPropListData from "../data/PlayerPropListData";
import { ListItem, ListItemIcon, ListItemText } from '@mui/material';
import styles from "./BetList.module.css";
import BetButtonGroup from './BetButtonGroup';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import PropListBody from './PropListBody';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';


interface ItemProps {
    id : number;
    name : string;      // player name
    type : string;      // moneyline, throwing yards etc...
    line : number;      // the over under line of throwing yards
    over : number;      // value of the over (i.e -250)
    under : number;     // value of the under
    overPct : number;   // Percentage of people that selected over
    underPct : number;   // Percentage of people that selected over
}

interface HeaderProps {
    id : number;
    expanded : boolean;
    setExpanded : Function;
    name : string;      // player name
    type : string;      // moneyline, throwing yards etc...
    line : number;      // the over under line of throwing yards
    over : number;      // value of the over (i.e -250)
    under : number;     // value of the under
    overPct : number;   // Percentage of people that selected over
    underPct : number;   // Percentage of people that selected over
}

const PropListHeader: React.FC<HeaderProps> = (props) => {
    const header = (
        <div className={styles.container}>
          <ListItem key={props.id}>
            <ListItemIcon onClick={() => props.setExpanded(!props.expanded)}>
                { props.expanded ? 
                    <ExpandMoreIcon></ExpandMoreIcon> : <ExpandLessIcon></ExpandLessIcon>
                }
            </ListItemIcon>
            <div className={styles.listInfo}>
                <ListItemText primary={props.name}></ListItemText>
                <ListItemText primary={'-'}></ListItemText>
                <ListItemText primary={props.type}></ListItemText>
            </div>
            <div className={styles.lineDiv}>
                <ListItemText className={styles.lineItem} primary={props.line}></ListItemText>
                <BetButtonGroup id={props.id}overPct={props.overPct} underPct={props.underPct}></BetButtonGroup>
            </div>
            </ListItem>
        </div>        
    )
    return header;
}

const PropBetListItem: React.FC<ItemProps> = (props) => {
    const [expanded, setExpanded] = useState<boolean>(false);
    return (
        <Fragment>
            <PropListHeader id={props.id} name={props.name} type={props.type} line={props.line} over={props.over} under={props.under} overPct={props.overPct} underPct={props.underPct} expanded={expanded} setExpanded={setExpanded} ></PropListHeader>
            <PropListBody id={props.id} expanded={expanded}></PropListBody>
        </Fragment>
        );        
};

export default PropBetListItem;
  
  